import axios from "axios"
import config from "@/config/common.config"
import { Toast } from 'vant'
// axios请求配置
const service = axios.create({
  baseURL: "",
  //timeout: 10000
})

service.defaults.withCredentials = true
service.defaults.headers['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8'  //设置请求头的类型

service.interceptors.request.use(configs => {
  // 如果是get请求，带上时间戳
  if (configs.method === "get") {
    configs.params = {
      _t: Date.parse(new Date()) / 1000,
      ...configs.params
    }
  }
  //为兼容小程序域名，修改内网后台地址
  // if(process.env.static !== 1) {
  //   config.POST.school_h5 = "https://t3.ais.cn"
  // }
  configs.url = configs.custom && configs.custom.isAis ? config.POST.ais_pc + configs.url : config.POST.school_h5 + configs.url
  return configs
})

service.interceptors.response.use(
  res => {
    if (res.data.code !== 0 && !process.server) {
      if (res.data.code === 1001) {
        // 检验是否未登录
        window.$nuxt.$store.commit("user/signOut")
        //window.$nuxt.$router.push('/')
        //console.log(`${config.POST.school_h5}/school/passport/login?state=${window.location.href}`)
        const url = encodeURIComponent(window.location.href)
        window.location.href = `${config.POST.school_h5}/school/passport/login?mobile=1&state=${url}`
        //window.location.replace(`${config.POST.school_h5}/school/passport/login?mobile=1&state=${url}`)
      } else {
        if(res.request.responseType !== 'arraybuffer'){
          Toast(res.data.codeMsg)
        }
      }
    }
    return res
  },
  error => {
    if (!process.server) {
      Toast('网络请求失败，请稍后重试')
    }
    return Promise.reject(error)
  }
)

export default {
  get (url, config) {
    return service.get(url, config)
  },
  post (url, params, config) {
    return service.post(url, params, config)
  }
}
