import { setCookie, getCookie } from 'ais-common-utils'
import { batchList } from '@/api/common'
import config from "@/config/common.config"
export default {
  data () {
    return {
      list: [],
      hide: false,
      hideRouter: ['/login', '/personal'],
      config
    }
  },
  watch: {
    $route () {
      this.hide = this.isShow()
    }
  },
  mounted () {
    if (!getCookie('rightPop')  && !this.$route.fullPath.includes('videoWeChatDetail')) {
      this.getbatchList()
    }
  },
  methods: {
    async getbatchList () {
      const { data } = await batchList('show_rightM_school')
      if (data.code === 0) {
        this.list = data.data.show_rightM_school.slice(0, 1)
        this.hide = this.isShow()
      }
    },
    isShow () {
      const finds = this.hideRouter.find(item => item === this.$route.path)
      if (!finds) return this.list.length > 0 && !getCookie('rightPop') 
      return false
    },
    closedHand () {
      setCookie('rightPop', '1', -1)
      this.hide = false
    },
    routerTo (item) {
      if (item.redirectUrl) {
        this.closedHand()
        window.open(item.redirectUrl, "_blank")
      }
    }
  }
}
