import Vue from 'vue'
import VueI18n from 'vue-i18n'

Vue.use(VueI18n)

const i18n = ( { app } ) => {
  app.i18n = new VueI18n({
    locale: 'zh',
    messages: {
      'en': { ...require('~/locales/en.json')},
      'zh': { ...require('~/locales/zh.json')}
    }
  })
}
export default i18n
