import config  from '@/config/common.config.js'
import { userAuth, logout } from '@/api/common'
import avatar from '@/assets/images/common/avator.png'
import { storage } from 'ais-common-utils'
export const state = () => ({
  userInfo: {}
})

export const actions = {
  // 获取个人资料信息
  async getUserInfoHand ({ commit }) {
    // if (Object.keys(state.userInfo).length) {
    //   return state.userInfo
    // }
    const { data } = await userAuth()
    if (data.code === 0) {
      const datas = {
        ...data.data,
        avatar:  data.data.avatar ? (data.data.avatar.includes('https') ? data.data.avatar : `${config.IMG_URL.default}${data.data.avatar}`) : avatar
      }
      commit('setUserInfo', datas)

      return datas
    }
    return {userStatus: false}
  },
  // 退出登录
  async signOut ({ commit }) {
    const { data } = await logout()

    if (data.code === 0) {
      commit('setSignOut')
      return true
    }
    return false
  }
}

export const mutations = {
  'setUserInfo' (state, data) {
    state.userInfo = data
    storage.set('userStatus', data.userStatus)
  },
  'setSignOut' (state) {
    state.userInfo = {}
    storage.remove('userStatus')
  }
}
