import { Locale } from 'vant'
export const state = () => ({
  locale: 'zh',
  lang:{}
})

export const mutations = {
  'setLang' (state, data) {
    state.lang = data
  },
  'setLocale' (state, data) {
    state.locale = data
    const { enUS, zhCN } = state.lang
    if(data === 'en') {
      Locale.use('en-US', enUS)
    }else {
      Locale.use('zh-CN', zhCN)
    }
  }
}