/*
** @Author: hlp
** @Description: 路由中间件，判断是否手机访问
*/
import config from "@/config/common.config"
import { checkDevice } from 'ais-common-utils'

export default  function ({ req, redirect }) {
  let flag = checkDevice(req)
  // 如果不是手机端
  if (!flag) {
    if (process.server) {
      req.statusCode = 302
      if(req && req.url && !req.url.includes('videoWeChatDetail') && !req.url.includes('courseCall')){
        redirect(`${config.LINK.school_pc}${req.url}`)
      }
    } else {
      window.location.href = `${config.LINK.school_pc}${window.location.pathname}${window.location.search}`
    }
  }
}
