/**通用接口 */
import request from "@/utils/request"
// import qs from "qs"

// 获取用户数据
export const userAuth = () => {
  return request.post(`/school/main/auth`)
}

// 获取用户数据
export const logout = () => {
  return request.post(`/school/passport/logout`)
}

// 批量获取广告
export const batchList = (code) => {
  return request.post(`/service/cms/pics/batch/${code}`, null, {
    custom: { // 自定义的额外参数
      isAis: true
    }
  })
}

// 获取一些通用选项卡数据
export const getOptionsList = (key) => {
  return request.post(`/school/common/options/${key}`)
}

//选项卡列表-批量,逗号隔开
export const getOptionsBatch = (options) =>{
  return request.post(`/school/common/optionsBatch/${options}`)
}
