import config from "@/config/common.config"
export default {
  data () {
    return {
      active: 0,
      tabList: [
        {icon: require('@/assets/images/common/home.png'), activeIcon: require('@/assets/images/common/homeActive.png'), path: '/', text: '首页'},
        {icon: require('@/assets/images/common/courseList.png'), activeIcon: require('@/assets/images/common/courseListActive.png'), path: '/allCourse', text: '所有课程'},
        {icon: require('@/assets/images/common/ais.png'), activeIcon: require('@/assets/images/common/aisActive.png'), path: config.LINK.ais_h5, text: '论文投稿'},
        {icon: require('@/assets/images/common/user.png'), activeIcon: require('@/assets/images/common/userActive.png'), path: '/account', text: '我的'}
      ]
    }
  },
  watch: {
    $route (to) {
      this.setActive(to.path)
    }
  },
  mounted() {
    this.setActive(this.$route.path)
  },
  methods: {
    setActive (path) {
      const index = this.tabList.findIndex(item => item.path === path)
      if (index > -1) {
        this.active = index
      }
    },
    changeTab (index) {
      index === 2 ? window.location.href = this.tabList[index].path : this.$router.push({path: this.tabList[index].path})
      this.active = index
    }
  }
}
